import React from "react";
import { Modal, Col, Form } from "react-bootstrap";
import { Button } from "semantic-ui-react";
import MessageModal from "../modals/MessageModal"

import { campaignService } from "../../services/campaignService.js"
import { ipfsService } from "../../services/ipfsService.js"

class ClosePorposalModal extends React.Component {

  state = {
    show: false,
    cpTitle: '',
    cpDescription: '',
    showMessage: false,
    message: "",
    createCPLoading: this.props.createCPLoading
  };

  create = async() =>  {
    if (true){
      try {
        const accounts = await campaignService.getAccounts();
        if(accounts.length===0) {
          const mssj = "Para crear un pedido debes haber iniciado sesin en la wallet de metamask.";
          this.setState({ showMessage: true, message: mssj});
          console.log(mssj);
        }
        else if (this.state.cpTitle.length < "5" || this.state.cpTitle.length > "30") {
          this.setState({ createCPLoading: false, 
            message: "El ttulo debe tener entre 5 y 30 caracteres.", 
            showMessage: true, 
            title: "Error"});
        }
        else if (this.state.cpDescription.length < "25" || this.state.cpDescription.length > "1000") {
          this.setState({ createCPLoading: false, 
            message: "La descripcin debe tener entre 25 y 1000 caracteres.", 
            showMessage: true, 
            title: "Error"});
        }
        else {
          this.setState({ createCPLoading: true});
          this.handleClose();
          const dateToday = new Date().toLocaleDateString('en-GB'); 
          const json_value =  {
            "title": this.state.cpTitle, 
            "description": this.state.cpDescription ,
            "created_date": dateToday
          }
          const ipfsHash = await ipfsService.addJson(json_value)
          const bytes32Hash = ipfsService.getBytes32Hash(ipfsHash);

          campaignService.createCloseProposal(bytes32Hash).then((statusResponse) => {
            let title, message = "";
            if (statusResponse.error) {
              title = "Hubo un error al crear elpedido";
              switch (statusResponse.errorMsg) {
                case "Accin denegada":
                  message = "Has denegado la accin a trves de metamask. Para que este completa debes aceptarla.";
                  break;
                case "Nonce error":
                  message = "Error de nonce: El nonce de la cuenta elegida y de la transaccin son diferentes.";
                  break;
                case "Gas insuficiente":
                  message = "La operacin llev ms gas que el que pusiste como lmite.";
                  break;
                default: 
                  message = "Error desconocido";
                  break;
              }
            }
            else {
              title = "Creacin exitosa";
              message = "Tu pedido de cierre fue creado correctamente!\n Gracias por involucrarte con la campaa!"; 
            }
            this.setState({ createCPLoading: false, 
                            message: message, 
                            showMessage: true, 
                            title: title});
          });
        }
      }
      catch(error)  {
        console.log(error);
      }
    }
    else {
      const msg = "La contribucin debe ser mayor a la mnima";
      this.setState({ message: msg, showMessage:true});
    }
  }

  handleClose = () => this.setState({ show: false});
  
  handleShow = () => this.setState({ show: true});

  handleMessageClose = () => this.setState({ showMessage: false});

  render() {
    return (
      <div>

        { this.state.showMessage &&
          <MessageModal
          showMessage={this.state.showMessage}
          handleMessageClose={this.handleMessageClose}
          message={this.state.message}
          title={this.state.title} />
        }
      
        <Button
        loading={this.state.createCPLoading}
        className="normal-button no-margin-top"
        onClick={this.handleShow}
        data-testid="closeProposalButton">
              Crear pedido
        </Button>
        
        <Modal show={this.state.show} onHide={this.handleClose}
        data-testid="closeProposalModal"
        aria-labelledby="contained-modal-title-vcenter"
        centered>
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">Nuevo Pedido de cierre</Modal.Title>
          </Modal.Header>
          <Modal.Body>
              <Form>
                  <Form.Group controlId="contributeBasicForm">
                      <Form.Label>Ttulo</Form.Label>
                      <Form.Control placeholder="Titulo pedido de cierre" onChange={event => this.setState({ cpTitle: event.target.value})}/>
                      <Form.Text className="text-muted">
                      *.
                      </Form.Text>
                  </Form.Group>
                  <Form.Group controlId="contributeBasicForm">
                      <Form.Label>Descripcin</Form.Label>
                      <Form.Control as="textarea" rows={5} placeholder="Razn para cerrar la campaa" onChange={event => this.setState({ cpDescription: event.target.value})}/>
                      <Form.Text className="text-muted">
                      *.
                      </Form.Text>
                  </Form.Group>
              </Form>
          </Modal.Body>
          <Modal.Footer className="footer-buttons">
              <Col lg={6} className="aling-left">
                  <button 
                      id="cerrar"
                      className="normal-button" 
                      onClick={this.handleClose}
                      data-testid="cancelButton">
                      Cerrar
                  </button>
              </Col>
              <Col lg={6} className="aling-right">
                  <button
                      className="normal-button" 
                      onClick={this.create}
                      data-testid="createCloseProposalButton">
                      Crear
                  </button>
              </Col>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

export default ClosePorposalModal;